/* Links */
a,
%joe__link,
.joe__link {
  color: $blue-darker;
  text-decoration: none;
  outline: 0;
  border-bottom: 1px solid $blue-lighter;
  transition: all .3s ease;

  &:hover,
  &:focus {
    color: $orange-darker;
    border-bottom: 1px solid $orange-lighter;
  }
}

%joe__link--black,
.joe__link--black {
  @extend %joe__link;
  color: $black-90;
  border-color: $black-80;
  
  &:hover,
  &:focus {
    color: $blue-darker;
    border-bottom: 1px solid $blue-lighter;
  }
} 
